@keyframes online-ind {
  0% {
    background-color: #3bff05;
  }
  50% {
    background-color: #5bd539;
  }
  100% {
    background-color: #3bff05;
  }
}
@keyframes header-in {
  from {
    transform: translateX(200%);
  }
  to {
    transform: translateX(0%);
  }
}
@keyframes app-vibrate {
  0% {
    transform: rotateZ(0deg);
  }
  20% {
    transform: rotateZ(-20deg);
  }
  35% {
    transform: rotateZ(5deg);
  }
  50% {
    transform: rotateZ(-20deg);
  }
  65% {
    transform: rotateZ(5deg);
  }
  80% {
    transform: rotateZ(-20deg);
  }
  90% {
    transform: rotateZ(5deg);
  }
  100% {
    transform: rotateZ(0);
  }
}
@keyframes sms-l1 {
  from {
    transform: scale(0);
  }
  to {
    transform: scale(1);
  }
}
